<template>
  <div class="register">
		<div v-if="step==1">
			<p style="font-size: 12px;color: #a5a5a5;background: #e5e5e5;padding:10px 20px;margin-top: 0;"> 
				注意：删除钱包前，请务必确认已备份好钱包助记词或私钥，否则将丢失你的钱包资产。
			</p>
			<mu-button @click="step=2" full-width style="height: 44px;margin-left: 8%;width: 84%;margin-top: 30px;" color="#295DF1">继续</mu-button>
		</div>
		
		<div v-if="step==2">
			<div style="margin: 40px;text-align: center;">
				<p style="font-size: 12px;color:#a5a5a5;">请输入备份的钱包助记词(12个英文单词)，按空格分离</p>
				<mu-text-field v-model="memorizingWords" style="padding: 10px;border: 1px solid #e5e5e5;" multi-line :solo="true" :rows="4" full-width></mu-text-field><br/>
				<mu-button @click="deleteWallet" class="creatBtn" style="width: 100%;margin-top: 40px;height: 44px;" color="#295DF1">删除钱包</mu-button>
			</div>
		</div>	
	
		<div v-if="step==3">
			<div style="margin:40px;text-align: center;">
			<i class="iconfont" style="font-size:100px;color: #295DF1;margin-top: 30px;">&#xe669;</i>
			<p>钱包删除成功!</p>
			<mu-button class="creatBtn" @click="entryWallet" style="width: 100%;margin-top: 40px;height: 44px;" color="#295DF1">进入钱包</mu-button>
		</div>
		</div>	
		</div>
  </div>
</template>
<script>
export default {
  name: "Regist",
  data() {
    return {
      step:1,
      memorizingWords:'',
    };
  },
  created(){
  	this.walletInfo = JSON.parse(this.$route.query.details);
  	this.secretSeed=this.walletInfo.secretSeed;
  },
  methods: {
		//删除钱包
		deleteWallet(){
			if(this.secretSeed == this.memorizingWords){
				let localStorageArr = JSON.parse(localStorage.getItem('walletArr'));
				for(let i in localStorageArr){
					if(localStorageArr[i].secretSeed == this.memorizingWords){
						localStorageArr.splice(i,1);
						localStorage.setItem('walletArr',JSON.stringify(localStorageArr))
					}
				}
				this.step=3
			}else{
				this.$layer.open({
					content: '助记词输入不正确'
				    ,skin: 'msg'
				    ,time: 2 //2秒后自动关闭
				})
			}
		},
		entryWallet(){
			this.$router.replace('/my');
//			this.$router.go(-2);
		}
  },
  
  computed: {
    
  },
  watch:{
  	
  }
};
</script>
<style scoped>
	
</style>




